<template>
  <div class="check-view">
    <header-view :title="title" :fixed="fixed"></header-view>
    <div class="check-context"><i class="payment-shop"></i><span>{{shopName}}</span></div>
    <div class="check-money"><span>输入消费金额(￥)</span><input type="number" v-model="money" @focus="isfocus()" ref="myinput"></div>
    <div class="keyboard-modal" v-on:click="removeKeyboard()">
      <footer class="keyboard" v-bind:class="{'keyboard-block':focus}">
        <div class="keyboard-item">
          <div v-on:click.stop="onClick(1)">1</div>
          <div v-on:click.stop="onClick(4)">4</div>
          <div v-on:click.stop="onClick(7)">7</div>
          <div v-on:click.stop="onClick('a')"></div>
        </div>
        <div class="keyboard-item">
          <div v-on:click.stop="onClick(2)">2</div>
          <div v-on:click.stop="onClick(5)">5</div>
          <div v-on:click.stop="onClick(8)">8</div>
          <div v-on:click.stop="onClick(0)">0</div>
        </div>
        <div class="keyboard-item">
          <div v-on:click.stop="onClick(3)">3</div>
          <div v-on:click.stop="onClick(6)">6</div>
          <div v-on:click.stop="onClick(9)">9</div>
          <div v-on:click.stop="onClick('.')">.</div>
        </div>
        <div class="keyboard-item">
          <div class="keyboard-delete" v-on:click.stop="onClick('d')"></div>
          <div class="check-btn" v-on:click.stop="toPay">确认支付</div>
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
  import header from '@/components/header/header'
  export default {
    components: {
      'header-view': header
    },
    data () {
      return {
        money: 0,
        isPointMoney: -1,
        title: '优惠买单',
        routerName: '',
        fixed: false,
        agreementText: '',
        rightText: '',
        focus: false,
        shopName: ''
      }
    },
    created: function () {
      if (this.$route.params.id) { // 第一次进来把当前后退的路由存起来
        localStorage.prev = this.$route.params.prev
        sessionStorage.shopName = this.$route.params.storeName
      }
      this.routerName = this.$route.params.prev ? this.$route.params.prev : localStorage.prev
      this.shopName = this.$route.params.id ? this.$route.params.storeName : sessionStorage.shopName
      this.focus = true
    },
    methods: {
      isfocus: function () {
        this.focus = true
      },
      removefocus: function (val) {
        if (val) {
          this.focus = false
        } else {
          return
        }
      },
      onClick: function (val) {
        var Smoney = String(this.money)
        if (val === 'a') {
        } else if (val === 'd') {
          if (Smoney !== '0' && this.isPointMoney !== 0) {
            var Dmoney = Smoney.substring(0, Smoney.length - 1)
            if (Dmoney === '') {
              Dmoney = 0
            }
            if (Dmoney && Dmoney.charAt(Dmoney.length - 1) === '.') {
              this.money = Dmoney.substring(0, Dmoney.length - 1)
              this.isPointMoney = 0
            } else {
              this.money = Dmoney
            }
          } else if (this.isPointMoney === 0) {
            this.isPointMoney = -1
          }
        } else if (val === '.') {
          if (this.isPointMoney === -1) {
            this.isPointMoney = 0
          } else {}
        } else {
          if (!this.isPointMoney) {
            this.money = Smoney + '.' + String(val)
            this.isPointMoney = 1
          } else {
            if (Smoney === '0') {
              this.money = val
            } else {
              this.money = Smoney + String(val)
            }
          }
        }
      },
      removeKeyboard: function () {
        this.removefocus(1)
      },
      toPay: function () {
        this.$router.push({
          name: 'pay',
          params: {
            shopName: '测试',
            money: this.money
          }
        })
      }
    }
  }
</script>

<style>
  /* 支付页面 */
  
  .check-view {
    height: 100%;
    background-color: white;
  }
  
  .check-context {
    position: relative;
  }
  
  .check-context:before {
    content: "";
    border-bottom: .01rem solid #E5E5E5;
    width: 100%;
    height: .01rem;
    position: absolute;
    bottom: 0;
  }
  
  .check-context span {
    color: #333333;
    font-weight: 500;
    position: absolute;
    top: 50%;
    transform: translate(10%, -50%);
  }
  
  .check-money {
    height: .45rem;
    line-height: .45rem;
    padding-left: .15rem;
    background-color: white;
    color: #333333;
    position: relative;
  }
  
  .check-money:before {
    position: absolute;
    content: "";
    left: 0rem;
    right: 0;
    bottom: 0;
    border-bottom: .01rem solid #E5E5E5;
  }
  
  .check-money>input {
    float: right;
    text-align: right;
    position: absolute;
    font-size: .16rem;
    right: .1rem;
    top: 50%;
    transform: translate(0, -50%);
  }
  
  .keyboard {
    width: 100%;
    position: fixed;
    bottom: 0;
    border-top: .01rem solid #bfbfbf;
    display: none;
  }
  
  .keyboard-item {
    height: 2.4rem;
    text-align: center;
    line-height: .60rem;
    flex: 1;
    border-right: .01rem solid #bfbfbf;
  }
  
  .keyboard-item:last-child {
    border-right: none;
  }
  
  .keyboard-item div {
    height: .6rem;
    text-align: center;
    line-height: .60rem;
    flex: 1;
    border-bottom: .01rem solid #bfbfbf;
  }
  
  .keyboard-item div:last-child {
    border-bottom: none;
  }
  
  .check-btn {
    height: 1.8rem!important;
    padding: .65rem 25%;
    font-size: .19rem;
    color: white;
    background-color: #ff9c25;
    line-height: .25rem!important;
  }
  
  .keyboard-delete {
    background: url(/static/image/pay/delete.png);
    background-size: 100% 100%;
  }
  
  .keyboard-block {
    display: flex;
  }
  
  .keyboard-modal {
    height: 100%;
  }
</style>


